import PropTypes from 'prop-types';
import React, { useEffect, useRef } from 'react';

//* ------------------------ Component's MainSlide -----------------------------

export const MainSlide = ({ baseUrl }) => {
	const videoRef = useRef(null);
	const getPath = (fileName) => {
		return `${baseUrl}/${fileName}`;
	};

	useEffect(() => {
		if (videoRef.current) {
			const video = videoRef.current;
			//   
			const isVideoInView = () => {
				const videoTop = video.getBoundingClientRect().top;
				return videoTop > -400;
			};
			//     ,     
			const playVideo = async () => {
				if (isVideoInView()) {
					try {
						await video.play();
						console.log(' ');
					} catch (err) {
						console.warn('   :', err);
					}
				} else {
					console.log('  ,  ');
				}
			};
			playVideo();

			//     
			const handleVideoClick = () => {
				if (video.paused) {
					video.play();
				} else {
					video.pause();
				}
			};

			//   
			const handleScroll = () => {
				const videoElement = videoRef.current;

				//          
				const videoTop = videoElement.getBoundingClientRect().top;

				//     
				if (videoTop < -400) {
					if (!video.paused) {
						video.pause();
					}
				} else if (videoTop > -400) {
					if (video.paused) {
						video.play();
					}
				}
			};

			//   
			video.addEventListener('click', handleVideoClick);
			window.addEventListener('scroll', handleScroll);

			//     
			return () => {
				video.removeEventListener('click', handleVideoClick);
				window.removeEventListener('scroll', handleScroll);
			};
		}
	}, []);



	return (
		<div className="main-video">
			<div className="main-video__body">
				<div className="main-video__box _img">
					<video
						ref={videoRef}
						id="player-id"
						playsInline
						className="video-js"
						preload="auto"
						loop
						muted
					>
						<source src={getPath('img/audio/showreel-1.mp4')}
							type="video/mp4" />
					</video>

				</div>

				<div className="main-video__content _container">
					<h1 className="main-slide__title el-slidetitle h1_01901">
						<span> </span>
						<span>  </span>
						<span> Media-Group</span> &nbsp;
					</h1>
					<div className="main-slide__text t_01901">
						    ,   
						      
						,      
						  ,  ,  
						.
					</div>
				</div>
			</div>
		</div>
	);
};

MainSlide.propTypes = {
	baseUrl: PropTypes.string.isRequired,
};
